import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react'
import { graphql, navigate } from 'gatsby'
import { useFormikContext } from 'formik';
import { FiArrowLeft } from "react-icons/fi";
import { IconButton, useToken, Icon } from '@chakra-ui/react'
import { max } from 'lodash';
import { StaticImage } from 'gatsby-plugin-image';
import AniLink from "gatsby-plugin-transition-link/AniLink"

import SwipeLink from '../components/SwipeLink'
import Box from '../components/Box'
import Text from '../components/Text'
import Flex from '../components/Flex'
import Button from '../components/Button'
import Container from '../components/Container'
import { Media, responsive } from '../components/ThemeProvider/theme';

import Layout from '../containers/Layout'
import { trackAnswer } from '../utils/trackEvent';
import score from '../json/score';
import { motion } from 'framer-motion';

const layout = 'fullWidth'
const placeHolder = 'dominantColor'

const picture = {
  1: <StaticImage alt="Q1" src="../image/q1.png" layout={layout} placeholder={placeHolder} />,
  2: <StaticImage alt="Q2" src="../image/q2.png" layout={layout} placeholder={placeHolder} />,
  3: <StaticImage alt="Q3" src="../image/q3.png" layout={layout} placeholder={placeHolder} />,
  4: <StaticImage alt="Q4" src="../image/q4.png" layout={layout} placeholder={placeHolder} />,
  5: <StaticImage alt="Q5" src="../image/q5.png" layout={layout} placeholder={placeHolder} />,
  6: <StaticImage alt="Q6" src="../image/q6.png" layout={layout} placeholder={placeHolder} />,
}

const calculateScore = Object.values(score)
const citys = Object.keys(score)

const Question = ({ data, pageContext: { id, isLast, length } }) => {
  const [isSubmitting, setSubmitting] = useState()
  const [resCity, setCity] = useState()
  const [teal] = useToken('colors', ['custom.teal'])
  const { values, setFieldValue } = useFormikContext()
  const resLinkRef = useRef()
  const photoRef = useRef()
  const answerNotMatch = useMemo(() => values.answers.length < id - 1, [id, values.answers.length])
  const handleSubmit = useCallback(() => {
    setSubmitting(true)
    const totolPoints = calculateScore.reduce((res, scores) => {
      const point = scores.reduce((total, num, i) => {
        total = total + (num[values.answers[i]] || +'0')
        return total
      }, 0)
      res.push(point)
      return res
    },[])
    const cityIndexs = totolPoints.reduce((res, num, i) => {
      if (num === max(totolPoints)) {
        res.push(i)
      }
      return res
    }, [])
    const city = citys[cityIndexs[Math.floor(Math.random()*cityIndexs.length)]]
    setFieldValue('points', max(totolPoints))
    setCity(city)

  }, [values, setFieldValue])
  useEffect(() => {
    if (resCity) {
      setTimeout(() => {
        if (resLinkRef.current) {
          resLinkRef.current.querySelector('a').click()
        }
      })
    }
  }, [resCity])
  useEffect(() => {
    if (answerNotMatch) {
      navigate(`/q/${values.answers.length + 1}`)
    }
  }, [answerNotMatch])
  // useEffect(() => {
  //   if (photoRef) {
  //     photoRef.current.querySelector('div').click()
  //   }
  //   console.log(photoRef.current)
  // }, [])
  if (answerNotMatch) return null
  return (
    <Layout
      noHeader
      bg="white"
      title={`第${id}題`}
      footerProps={{
        position: responsive('static', 'absolute'),
        transform: responsive('none', 'translateX(-50%)'),
        left: '50%',
        bottom: '1.5em',
        py: responsive('1.25em', '0'),
      }}
    >
      <Container pt={responsive('1em', '3.5em')}>
        <Box mx={responsive('auto', 0)} width={responsive('6.375em', '8.625em')}>
          <AniLink to="/">
            <StaticImage src="../containers/teal_logo.png" layout="fullWidth" alt="GoShare Logo" placeholder="tracedSVG" tracedSVGOptions={{ color: '#06F3F9' }} />
          </AniLink>
        </Box>
        <Flex alignItems="flex-end" justifyContent="space-around">
          <Box width="480px" as={Media} greaterThanOrEqual="tablet">
            {picture[id]}
          </Box>
          <Box width={responsive('100%', '442px')}>
            <Flex
              pb="0.25em"
              alignItems="center"
              justify="space-between"
              borderBottom="1px solid"
              borderColor="custom.borderGray"
            >
              <IconButton
                as={SwipeLink}
                w={responsive('1.5em', '2em')}
                h={responsive('1.5em', '2em')}
                mr="0.25em"
                minWidth="auto"
                bg="transparent"
                color="custom.darkBlue"
                icon={<Icon as={FiArrowLeft} w={responsive(6, 8)} h={responsive(6, 8)} />}
                to={+id > 1 ? `/q/${id - 1}` : '/'}
                isDisabled={isSubmitting}
                _hover={{
                  bg: 'transparent'
                }}
              />
              <Text
                fontSize={responsive('1.5rem', '2rem')}
                letterSpacing="0.125em"
                fontFamily="Flama"
                color="lightGray"
              >
                <Text.Inline color="gray">{id}</Text.Inline>/{length}
              </Text>
            </Flex>
            <Text
              mt={responsive('0.5em', '2em')}
              fontWeight="500"
              color="custom.darkBlue"
              fontSize={responsive('1.125em', '1.375em')}
            >{data?.questionsJson.question}</Text>
            <Box ref={photoRef} as={Media} lessThan="tablet" pt="1em">
              {picture[id]}
            </Box>
            <Box>
              {data?.questionsJson.options.map((opt, i) => (
                <Box as={motion.div} whileTap={{ scale: 1.1 }} mt={responsive(i && '1em', i ? '1.25em' : '3.25rem')} key={i}>
                  <Button
                    as={!isLast && ((p) => <SwipeLink {...p} swipe direction="left" top="entry" delay={0.5} to={`/q/${+id + 1}`} />)}
                    onClick={() => {
                      if (!resCity) {
                        setFieldValue(`answers.${id - 1}`, i)
                        trackAnswer(id, i)
                        if (isLast) {
                          handleSubmit(i)
                        }
                      }
                    }}
                    width="100%"
                    isDisabled={isSubmitting}
                    isActive={values.answers[id - 1] === i}
                  >
                    {opt}
                  </Button>
                </Box>
              ))}
            </Box>
          </Box>
        </Flex>
      </Container>
      <Box.Absolute opacity="0" width="0" height="0" overflow="hidden" ref={resLinkRef}>
        <AniLink tabIndex="-1" paintDrip hex={teal} to={`/result/${resCity}`} />
      </Box.Absolute>
    </Layout>
  )
}

export default Question

export const query = graphql`
  query QuestionPageQuery($id: String) {
    questionsJson(id: {eq: $id}) {
      question
      options
    }
  }
`
